/**
 * @jest-environment jsdom
 */
require('@testing-library/jest-dom');
const React = require('react');
const { render, screen } = require('@testing-library/react');

// TIDAK PERLU jest.mock. Kita akan menguji komponen Profile yang asli.

// Mengambil kode mahasiswa
if (!process.env.SUBMISSION_PATH) {
  throw new Error('SUBMISSION_PATH environment variable not set.');
}
const Profile = require(process.env.SUBMISSION_PATH).default;


describe('Praktikum: Komponen Profile', () => {

    beforeEach(() => {
        render(<Profile />);
    });

    test('Kriteria 1: Harus menampilkan sebuah elemen gambar (image)', () => {
        const image = screen.getByRole('img');
        expect(image).toBeInTheDocument();
    });

    test('Kriteria 2: Gambar harus memiliki teks alternatif (alt) yang benar', () => {
        // Kita cari gambar berdasarkan alt text yang diharapkan
        const image = screen.getByAltText('Katherine Johnson');
        expect(image).toBeInTheDocument();
    });

    test('Kriteria 3: Gambar harus memiliki ukuran width dan height yang benar', () => {
        const image = screen.getByAltText('Katherine Johnson');
        expect(image).toHaveAttribute('width', '100');
        expect(image).toHaveAttribute('height', '100');
    });

    test('Kriteria 4: Komponen "Profile" harus diexport dengan benar', () => {
        // Pastikan komponennya tidak undefined
        expect(Profile).toBeDefined();
    });
});
